import React, { Component } from 'react';
import inside from 'point-in-polygon'
import wallPolygon from './wall-polygon'
import './App.css';

// const definitelyInside = [52.4694, 13.3278]
// console.log(inside(definitelyInside, wallPolygon))

class App extends Component {
  state = {
    err: null
  }

  handleClick = () => {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition(function(position) {
        const { latitude, longitude } = position.coords;
        const inWest = inside([latitude, longitude], wallPolygon)
        this.setState({inWest})
      });
    } else {
      this.setState({
        err: 'Geolocation unavailable '
      })
    }
  }
  render() {
    const { inWest, err } = this.state;
    const msg = typeof inWest === 'boolean' && (
      inWest ? 'You\'re in West Berlin!' : 'You\'re in East Berlin!'
    )
    return (
      <div className="App">
        <button onClick={this.handleClick}>Am I in West Berlin?</button>
        <div>{ msg }</div>
        { err && <div><b>{err}</b></div>}
      </div>
    );
  }
}

export default App;
